		aboutUsContent:'Vi skapar naturupplevelser och guidar dig i Södra Södermanland. Trosa, Nyköping, Kolmården och Stendörren är några av våra guldkorn. Vår mission är att ge dig en aktiv naturupplevelse. Oavsett om du kommer från utlandet eller är infödd sörmlänning så har vi något för dig. Och känner du att Sverige börjar bli för litet så följ med på någon av våra unika utlandsresor där vi i små grupper upplever riktiga äventyr tillsammans, fortfarande anpassade så att alla kan vara med.',
		enviromentStatement:'Enviroment-statement',
		enviromentTextOne:'Att år 2018 driva ett reseföretag som bygger på flyg är ju bland det dummaste man kan göra med tanke på den negativa påverkan flyget har på vår jord. Jag vet! Att folk flyger, är ju en förutsättning för vår verksamhet. Därför har vi tagit fram följande miljö-statement för vår verksamhet. Det ursäktar inte den miljöpåverkan våra kunder har via sina flygresor men det är vår insats till miljön, då vi tror att lusten att upptäcka nya saker inte begränsas av om vi driver detta företag eller inte. ',
		enviromentTextTwo:'Vi klimatkompenserar för samtliga våra gästers flygresor. Vi arrangerar våra resor utifrån ett långsiktigt och hållbart tänkt för att minimera skador och negativ påverkan på miljön. Som exempel ingår en kort miljöintroduktion när vi går igenom din kommande tur.',
